.optionsFilter-list::after {
   content: '';
   position: absolute;
   width: 364px;
   height: 0px;
   border: 1px solid #e5e5e5;
   right: -38px;
   bottom: 0px;
}

.optionsFilter-item {
   display: flex;
   align-items: center;
}
.optionsFilter-icon {
   display: flex;
   align-items: center;
   width: 58px;
}

