}

.optionsFilter-form {
   display: flex;
   align-items: center;
   margin-left: auto;
}
.optionsFilter-checkbox {
   height: 0;
   width: 0;
   visibility: hidden;
}

.optionsFilter-switch-label {
   display: flex;
   align-items: center;
   justify-content: space-between;
   cursor: pointer;
   width: 72px;
   height: 19px;
   border-radius: 10px;
   background: #ffffff;
   position: relative;
   transition: background-color 0.2s;
}
.isOn {
   background: #fcdc9d;
}

.optionsFilter-switch-label .optionsFilter-switch-button {
   content: '';
   position: absolute;
   top: -5px;
   left: 0px;
   width: 28px;
   height: 28px;
   border-radius: 45px;
   transition: 0.2s;
   background: #c4c4c4;
   box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29);
}

.optionsFilter-checkbox:checked
   + .optionsFilter-switch-label
   .optionsFilter-switch-button {
   left: calc(100% - 0px);
   transform: translateX(-100%);
   background: #ffa800;
}

