.details {
   background: #3e3c41;

   margin-bottom: 95px;
   padding: 35px 28px 0 28px;
}

.details_title {
   font-weight: 400;
   font-size: 30px;
   line-height: 35px;
   text-transform: uppercase;
   color: #e5e5e5;
   margin: 0;
   text-align: center;
   position: relative;
}

.details_title::after {
   content: '';
   position: absolute;
   width: 364px;
   height: 0px;
   border-bottom: 1px solid #e5e5e5;
   right: -28px;
   bottom: -33px;
}

.title--small {
   font-weight: 700;
   margin-left: 40px;
   margin-top: 70px;
   margin-bottom: 0;
}

.sidebar_date {
   margin-left: 10px;
   font-weight: 400;
   font-size: 18px;
   line-height: 21px;
}

.sidebar_header {
   position: relative;
   display: flex;
   align-items: flex-end;

   /* height: 60px; */

   /* margin-bottom: 50px; */
}

.title-goThere::before {
   position: absolute;
   content: '';
   background-image: url(../../../img/sideBar/from.svg);
   background-repeat: no-repeat;
   width: 32px;
   height: 26px;
   bottom: 4px;
   left: 0;
}

.title-goBack::before {
   position: absolute;
   content: '';

   background-image: url(../../../img/sideBar/to.svg);
   background-repeat: no-repeat;
   width: 32px;
   height: 26px;
   bottom: 4px;
   left: 0;
}

.active-button {
   bottom: 0;
}

.details_button {
   width: 20px;
   height: 20px;
   padding: 0;

   box-sizing: border-box;
   border-radius: 5px;
   background-color: inherit;
   position: relative;
   margin-bottom: 3px;
   margin-left: auto;

   border: 2px solid #c4c4c4;
}
.details-wrapper {
   margin-top: 32px;
}

.details_info {
   display: flex;

   font-weight: 400;
   font-size: 18px;
   line-height: 21px;

   color: #e5e5e5;
}
.details_info-title {
   margin: 0;
}
.details_info-text {
   margin: 0;
   margin-bottom: 22px;
   margin-left: auto;
   color: #ffffff;
}

.details_info-text--train {
   font-weight: 700;
   font-size: 24px;
   line-height: 28px;
   color: #ffffff;
}

.details_info-text--route {
   text-transform: capitalize;
   text-align: right;
}

.details_info--route {
   display: block;
   margin-top: 46px;
}
.route_time {
   font-weight: 700;
   font-size: 24px;
   line-height: 28px;

   color: #ffffff;
}

.route {
   display: flex;
   justify-content: space-between;
   margin-top: 17px;
}

.route_time {
   margin: 24px 0 0 0;
   /* margin: 0; */
}
.route_duration {
   text-align: center;
}
.route_duration-time {
   margin: 0 0 7px 0;
}
.route_date {
   margin: 3px 0 0 0;
   color: #928f94;
}

.route_city {
   font-weight: 400;
   font-size: 18px;
   line-height: 21px;
   color: #ffffff;
   margin: 0;
   text-transform: capitalize;
}

.route_info--right {
   text-align: right;
}

.route_station {
   margin: 0 0 20px 0;
   font-weight: 400;
   font-size: 16px;
   line-height: 19px;
   text-transform: capitalize;
   color: #928f94;
}

.sidebar_inner {
   position: relative;
}

.sidebar_inner::after {
   content: '';
   position: absolute;
   width: 364px;
   height: 0px;
   border-bottom: 1px solid #e5e5e5;
   right: -28px;
   bottom: -33px;
}

.title-passengers::before {
   position: absolute;
   content: '';

   background-image: url(../../../img/sideBar/person.svg);
   background-repeat: no-repeat;
   width: 32px;
   height: 26px;
   bottom: 4px;
   left: 0;
}

.details_passenger-list {
   list-style: none;
   padding: 0;
   margin: 0;

   font-weight: 400;
   font-size: 18px;
   line-height: 21px;

   margin-top: 32px;

   color: #e5e5e5;
}

.details_passenger-item {
   display: flex;
   align-items: center;
   margin-bottom: 22px;
}

.details_passenger-count {
}

.details_passenger-price-value {
   font-weight: 700;
   font-size: 24px;
   line-height: 28px;

   color: #ffffff;
   margin-left: auto;
}

.details-сurrency {
   height: 17px;
   margin-left: 5px;
}

.details_footer-title {
   margin-left: 0;
}

.details_footer {
   display: flex;
   align-items: baseline;
   justify-content: space-between;
   padding-bottom: 33px;
}

.details_footer-total-value {
   font-weight: 700;
   font-size: 48px;
   line-height: 56px;
   margin-left: auto;
   color: #ffa800;
}

.details-сurrency-total {
   margin-left: 10px;
   height: 32px;
}
